.row-poster {
  width: 100%;
  object-fit: contain;
  max-height: 100px;
  transition: transform 450ms;
  margin-right: 10px;
}
.row-posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
  transition: transform 450ms;
}
.row-posters::-webkit-scrollbar {
  display: none;
}
.row-poster:hover {
  transform: scale(1.08);
}
.row-posterLarge {
  max-height: 250px;
}
.row-posterLarge:hover {
  transform: scale(1.09);
}
.row {
  margin-left: 20px;
  color: white;
}
h1 {
  font-size: 25px;
}
