  z-index: 1;
  
}
 .nav {
  position: fixed;
  left: 20px;
  width: 100%; 
  object-fit: contain;
  background-color: white;
} 
.header-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* .nav-black {
  background-color: rgb(20, 20, 20);
} */
.header-left ul {
  /* max-width:140%; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  list-style: none;
  cursor: pointer;
  font-size: 0.90em;
}
.header-left ul li {
  padding-left: 0.75rem;
}
.header-right ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
  list-style: none;
  font-size: 0.75em;
}
.header-right ul li {
  padding-left: 0.75rem;
}
